p{
    padding: 0px;
    margin: 0px;
}

:root {
    --main: #bc5148;
    --secondary : #f6f6e9;
    --black : #222831;
    --light : #efefef;
    --main-dark : #194769;
}



.sporteens-main{
    color: var(--main);
}

.sporteens-main-dark{
    color: var(--main-dark);
}

.sporteens-secondary{
    color: var(--secondary);
}

.sporteens-black{
    color: var(--black);
}

.sporteens-light {
    color: var(--light);
}



.sporteens-bg-main{
    background-color: var(--main);
}


.sporteens-bg-main-dark{
    background-color: var(--main-dark);
}

.sporteens-bg-secondary{
    background-color: var(--secondary);
}

.sporteens-bg-black{
    background-color: var(--black);
}

.sporteens-bg-light {
    background-color: var(--light);
}

/* Fonts Utils */

.sporteens-font-8{
    font-size: 8px;
}

.sporteens-font-10{
    font-size: 10px;
}

.sporteens-font-12{
    font-size: 12px;
}
.sporteens-font-14{
    font-size: 14px;
}

.sporteens-font-16{
    font-size: 16px;
}

.sporteens-font-20{
    font-size: 20px;
}


/* Shadow */

.sporteens-shadow{
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.5);
}

.sporteens-input:focus{
    outline: none;
}


/* Garis horizontal */

.sporteens-text-ditengah-line{
    width: 80%; 
    border-bottom: 1px solid #000; 
    line-height: 0.1em;
    
}

.border-double {
    border: 1px solid black;
    position: relative;
    padding: 12px;
    /* background-color: salmon; */
}

.border-double::after {
    content: " ";
    position: absolute ;
    z-index: -1;
    border: 1px solid black;
    top: -5px;
    left: -5px;
    right: 3px;
    bottom: 3px;
    padding: 5px;
}

/* Link */

.my-link{
    color: inherit;
    text-decoration: none;
}
.my-link:hover{
    color: inherit;
    text-decoration: none;
}

/* hover */



.sporteens-onhover:hover{
    font-weight: bold;
}

/* container */

.instinct-container {
    margin: 0 60px;
}