.sporteens-jumbotron{
    height: 650px;
    
}

.jumbotron-text{
    z-index: 1;
    text-shadow: 1px 1px 1px rgba(27,38,44,0.8);
}

.sporteens-carousel-1{
    background-image: url('https://images.unsplash.com/photo-1581088293793-0e9f1cae5bed?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2263&q=80');
    background-size: cover;
    background-position: center;
}

.sporteens-carousel-2{
    background-image: url('https://images.unsplash.com/photo-1561613441-a21266ca81fa?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2689&q=80');
    background-size: cover;
    background-position: center;
}

.sporteens-carousel-3{
    background-image: url('https://images.unsplash.com/photo-1587683437362-da7775ffc532?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2102&q=80');
    background-size: cover;
    background-position: center;
}

.sporteens-jumbotron::after{
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to top, rgba(0,0,0,1), rgba(0,0,0,0));
    position: absolute;
    bottom: 0;
}

.sporteens-bg-banner{
    background-image: linear-gradient(to top, #364f6b 0%, #71c9ce 100%, #71c9ce 100%, #71c9ce 100%);
}


/* flas sale */

.sporteens-thumbnail-img{
    max-width: 100%;
    
    z-index: -1;
    
    
}
.sporteens-thumbnail-border{
    margin: 5px 5px 0 5px;
    height: 200px;
}


.my-landing-page-font  {
    font-size: 20px;
    letter-spacing: 1px;
}
.image-font-flash-sale-title{
    font-size: 14px;
}
.image-font-flash-sale-isi{
    font-size: 12px;
}

.landing-page-text-child {
    font-size: 14px;
    margin-top: 20px;
}
.text-brands {
    font-size: 14px;
    letter-spacing: 1px;
    text-align: center;
    margin-top: 20px;
    
}


/* Phone */
@media (max-width: 767.98px) { 
    .sporteens-jumbotron{
        height: 300px;
    }
    .text-jumbotron{
        font-size: 24px;
    }

    .my-landing-page-font  {
        font-size: 17px;
    }
    .image-font-flash-sale-title{
        font-size: 12px;
    }
    .image-font-flash-sale-isi{
        font-size: 10px;
    }
    
 }

 .colabs::after{
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    /* background-image: linear-gradient(to top, rgba(0,0,0,1), rgba(0,0,0,0)); */
    background-color: black;
    opacity: 0.6;
    position: absolute;
    bottom: 0;
}